// routes of navigation
export default [
  {
    code: 'navigation',
    name: '',
    children: [
      {
        path: '/component/menu',
        name: 'Menu',
        meta: {
          type: 'component',
          title: 'Menu '
        },
        component: () => import('../../docs/zh_CN/menu.md')
      },
      {
        path: '/component/breadcrumb',
        name: 'Breadcrumb',
        meta: {
          type: 'component',
          title: 'Breadcrumb '
        },
        component: () => import('../../docs/zh_CN/breadcrumb.md')
      },
      {
        path: '/component/dropdown',
        name: 'Dropdown',
        meta: {
          type: 'component',
          title: 'Dropdown '
        },
        component: () => import('../../docs/zh_CN/dropdown.md')
      },
      {
        path: '/component/pagination',
        name: 'Page',
        meta: {
          type: 'component',
          title: 'Pagination '
        },
        component: () => import('../../docs/zh_CN/pagination.md')
      },
      {
        path: '/component/steps',
        name: 'Steps',
        meta: {
          type: 'component',
          title: 'Steps '
        },
        component: () => import('../../docs/zh_CN/steps.md')
      }
    ]
  }
]



