<template>
  <span
    :class="['mku-slider__button', {'mku-slider__button--active': isDrop}]"
    :style="buttonStyle"
    @mousedown="handleDragStart"
    @mousemove="handleDraging"
    @mouseup="handleDragEnd">
  </span>
</template>

<script>
import { withinNum, floor } from '../../../utils/tools'
import { onEvent, offEvent, getRect } from '../../../utils/dom'
import { isArray } from '../../../utils/assist'

export default {
  name: 'MkuSlider',
  props: {
    // 
    value: {
      type: Number
    }
  },
  data () {
    return {
      isDrop: false, // 
      currentX: 0 // 
    }
  },
  computed: {
    step () {
      return this.$parent.step
    },
    min () {
      return this.$parent.min
    },
    max () {
      return this.$parent.max
    },
    disabled () {
      return this.$parent.disabled
    },
    // range
    rangeDiff () {
      return this.max - this.min
    },
    // 
    buttonStyle () {
      return {
        left: (this.value - this.$parent.min) / this.rangeDiff * 100 + '%'
      }
    }
  },
  methods: {
    /**
     * @method handleDragStart
     * @description 
     * - slidersize
     * - slidervalue
     * - moveend
     */
    handleDragStart (event) {
      if (this.disabled) return
      event.preventDefault() // 
      this.startX = event.clientX
      this.$parent.calcSliderSize()

      this.isDrop = true
      this.$parent.cacheCurrentValue()

      onEvent(window, 'mousemove', this.handleDraging)
      onEvent(window, 'mouseup', this.handleDragEnd)
    },
    /**
     * @method handleDraging
     * @description 
     * - slider
     * - value
     */
    handleDraging (event) {
      if (this.disabled) return
      if (!this.isDrop) return

      this.currentX = event.clientX
      const offsetWidth = this.currentX - this.$parent.startX // 
      this.updateValue(offsetWidth)
    },
    /**
     * @method updateValue
     * @description value
     * - slider
     * - value
     */
    updateValue (offsetWidth) {
      const stepWidth = this.step / this.rangeDiff * this.$parent.sliderWidth // step
      const stepsCount = Math.round(offsetWidth / stepWidth) // stepround
      let value = stepsCount * this.step + this.min //  *  + min
      value = withinNum(value, this.min, this.max) // minmax
      this.$emit('input', value)
    },
    /**
     * @method handleDragEnd
     * @description 
     * - moveend
     * - drag-change
     */
    handleDragEnd () {
      if (this.disabled) return
      this.isDrop = false

      if (this.$parent.checkValueChange()) {
        this.$parent.emitEvent('drag-change', this.$parent.getEmitValue())
      }

      offEvent(window, 'mousemove', this.handleDraging)
      offEvent(window, 'mouseup', this.handleDragEnd)
    }
  }
}
</script>



