import { isNumber } from './assist'

// 
export const merge = (target, ...args) => {
  args.forEach(arg => {
    for (let prop in arg) {
      if (arg.hasOwnProperty(prop)) {
        if (arg[prop] !== undefined && arg[prop] !== null) {
          target[prop] = arg[prop]
        }
      }
    }
  })
  return target
}

// px
export const formatSize = val => {
  return isNumber(val) ? val + 'px' : val
}

// TODO
export const isEqual = (a, b) => {
  if (typeof a !== typeof b) return false
  return a === b
}

// 100
export const fillZero = num => {
  if (!num && num !== 0) return ''
  if (num < 10) return `0${num}`
  return num
}

// maxmaxminmin
export const withinNum = (origin, min, max) => {
  return Math.min(Math.max(origin, min), max)
}

// 
export const floor = (num) => {
  return Math.floor(num)
}

/**
 * @method pickNumberInString
 * @description 
 * @param { String } str 
 * @returns 
 */
export const pickNumberInString = str => {
  const timeString = str.replace(/[^\d]+/g, v => ',')
  return timeString.split(',')
    .filter(e => e !== '')
    // 0, 
    .map((e, i) => parseInt(e))
    // .map((e, i) => withinNum(parseInt(e), 0, i === 0 ? 23 : 59))
}

/**
 * @method compareArrJoined
 * @description 2joineg[1, 2, 3] => 123[4,5,6] => 456, 123 < 456
 * @returns arr1 > arr2
 */
export const compareArrJoined = (arr1, arr2) => {
  const reg = /[^\d]+/g
  const remove = str => str.replace(reg, e => '')
  const startNum = parseInt(remove(arr1.join(''))) || 0
  const endNum = parseInt(remove(arr2.join(''))) || 0
  return startNum > endNum
}

export const fullArray = (arr, len, val) => {
  let res = []
  for (let i = 0; i < len; i++) res[i] = arr[i] !== undefined ? arr[i] : val
  return res
}



