<template>
  <div :class="radioGroupClasss">
    <slot></slot>
  </div>
</template>

<script>
import Emitter from '../../../utils/emitter'

export default {
  name: 'MkuRadioGroup',
  mixins: [Emitter],
  props: {
    // radio-group
    value: {
      type: [Number, String, Boolean]
    },
    // 
    mode: {
      type: String,
      default: 'horizontal',
      validator: val => {
        return ['horizontal', 'vertical'].includes(val)
      }
    },
    type: {
      type: String,
      default: 'radio',
      validator: val => {
        return ['radio', 'button'].includes(val)
      }
    },
    // button-groupbutton
    buttonType: {
      type: String,
      default: 'line',
      validator: val => {
        return ['line', 'fill'].includes(val)
      }
    },
    size: {
      type: String,
      default: 'default',
      validator: val => {
        return ['large', 'default', 'small'].includes(val)
      }
    }
  },
  data () {
    return {
      radioGroupValue: this.value
    }
  },
  watch: {
    value (newVal) {
      this.radioGroupValue = newVal
    }
  },
  computed: {
    // radio-groupclassName
    radioGroupClasss () {
      return {
        'mku-radio-group': this.type === 'radio',
        'mku-radio-group--vertical': this.mode === 'vertical',
        'mku-button-group': this.type === 'button',
        'mku-button-group--fill': this.type === 'button' && this.buttonType === 'fill',
        'mku-button-group--lg': this.size === 'large',
        'mku-button-group--sm': this.size === 'small'
      }
    }
  },
  methods: {
    /**
     * @method change
     * @description radio-group
     * - value
     * - change
     * - Form
     */
    change (val) {
      this.radioGroupValue = val
      this.$emit('input', val)
      this.$emit('change', val)
      this.dispatch('MkuFormItem', 'onFormItemChange', val)
    }
  }
}
</script>



